import React, { Component } from "react";
import "./header.css";
import "./Nav.css";
import About from "./about";

class Nav extends Component {
  state = {};

  render() {
    return (
      // REMEMBER TO ADD A # tag infront of every a href link like in "About" otherwise react routing will not work on Github pages.
      <div>
        <div className="container-fluid">
          <div className="row justify-content-between">
            <div className="col-0 my-col-nav2">
              <div className="nav navbar-nav d-md-block">
                <a href="../html-link.htm">
                  <img src="https://image.flaticon.com/icons/svg/1077/1077041.svg" className="img-responsive" width="24" height="24" title="White flower" alt="Flower" />
                </a>
                <a href="../html-link.htm">
                  <img src="https://image.flaticon.com/icons/svg/87/87390.svg" className="img-responsive" width="24" height="24" title="White flower" alt="Flower" />
                </a>
                <a href="../html-link.htm">
                  <img src="https://image.flaticon.com/icons/svg/1051/1051382.svg" className="img-responsive" width="24" height="24" title="White flower" alt="Flower" />
                </a>
              </div>
            </div>
            <div className="col-md-0 my-col-nav">
              <ul className="nav navbar-nav d-none d-md-block">
                <b>
                  <a className="text-dark text-decoration-none" href="/">
                    <span className="" /> Home
                  </a>
                  <a className="text-dark text-decoration-none" href="/#about">
                    <span className="glyphicon glyphicon-user" /> About
                  </a>
                  <a className="text-dark text-decoration-none" href="#">
                    <span className="glyphicon glyphicon-user" /> Portfolio
                  </a>
                  <a className="text-dark text-decoration-none" href="#">
                    <span className="fa fa-facebook" /> Contact
                  </a>
                </b>
              </ul>
              <div className="dropdown d-md-none my-col-nav-collapsed">
                <button className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  Navigation
                </button>
                <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                  <a className="dropdown-item" href="#">
                    Home
                  </a>
                  <a className="dropdown-item" href="/#about">
                    About
                  </a>
                  <a className="dropdown-item" href="#">
                    Portfolio
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Nav;
