import React, { Component } from "react";
import "./about.css";

class About extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className="container-fluid">
          <div className="row justify-content-center bg-dark">
            <div className="col-0 text-white">
              <h1>About Me</h1>
            </div>
          </div>
          <div className="row justify-content-center bg-dark">
            <div className="col-0 text-white">
              <img src="./images/portrait.jpg" className="img-fluid img-thumbnail img-circle" width="200px" alt="Image of me" />
            </div>
          </div>
          <div className="row justify-content-center bg-dark">
            <div className="col-0 text-white my-col-about">
              My name is Jonas Støve Rasmussen and I am studying at <a href="https://www.sdu.dk/en">University of Southern Denmark (SDU)</a> to become a Software Engineer. My goal is to get my Master's Degree by the year of 2021.
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
